.Card:hover{
  transform: scale(1.07);
}

.Card{ display: flex;
  flex: 0 0 auto;
  width: 200px;
  flex-direction: column;
  justify-content: center;
  position: relative;
  color: white;
  background-color: black;
  margin-bottom: 4px;}

  .Card img{position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 0;
    opacity: 0.7;}